}


body {
  font-family: 'Comfortaa', cursive;
  font-size: 18px;
}

.app {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
}

.body {
  flex: 0 1 100%;
  width: 100%;
}

body, html {
  margin: 0;
  padding: 0;

}

/* animation */
.my_div {

  transition: all 500ms ease-in-out;
}

.old-text,
.new-text {
  display: block;
  overflow: hidden;
  transition: all 500ms ease-in-out;
}
.old-text {
  height: auto;
  opacity: 1;
  width: auto;
}
.new-text {
  background-image: linear-gradient(90deg, #c793cb, #ec8c9c);
  color: white;
  height: 0;
  opacity: 0;
  width: 0;
}
.my_div:hover .old-text {
  height: 0px;
  opacity: 0;
  width: 0px;
}
.my_div:hover .new-text {
  height: auto;
  opacity: 1;
  width: auto;
}
/* Change Selection Text Color When Highlighting */ 
  ::selection { background-color: #EEA6BC; 
  color: #fff; }
